<!--教材内容-->
<template>
     <div class="page-template">
            <!--页面title-->
           <el-row class="kno-title">
                <el-col :span="12"><div class="grid-content bg-purple cha-title">{{ pageTile }}</div></el-col>
                <el-col :span="12"><div class="grid-content bg-purple-light">
                    <bnt-list :model="model" @btn-click="knoAdd(1)"></bnt-list>
                    <bnt-list :model="modela" @btn-click="knoAdd(2)"></bnt-list>
                    <bnt-list :model="modelc" @btn-click="knoAdd(4)"></bnt-list>
                </div></el-col>
           </el-row>
            <!--顶部查询-->
            <el-form ref="formInline"  :inline="true" :model="formInline" class="demo-form-inline">
                <el-form-item>
                    <el-input v-model="formInline.bookname" placeholder="请输入教材名称"></el-input>
                </el-form-item>
                <el-form-item label="学科:">
                    <el-select v-model="formInline.discipline" placeholder="不限" style="width: 200px;">
                        <el-option v-for="item in discipline" :key="item.id" :label="item.name" :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="教材版本:">
                    <el-select v-model="formInline.bookversion" placeholder="不限" style="width: 200px;">
                         <el-option  v-for="item in versiondata" :key="item.id" :label="item.name" :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <button class="primary-btn" @click="search(1)">查询</button>
                    <button class="clear-btn" @click="search(2)">清空</button>
                </el-form-item>
            </el-form>

            <!--表格-->
            <el-table class="base-table" :data="tableData" tooltip-effect="dark" style="width: 100%"  @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55">
            </el-table-column>
            <el-table-column type="index" label="序号" width="120">
            </el-table-column>
            <el-table-column  label="名称" width="120" >
                <template slot-scope="scope">
                  <router-link to=""  @click.native="goDetails(scope.row)" class="namefontcolor">{{scope.row.contentName }} </router-link>
                </template>
            </el-table-column>
            <el-table-column prop="periodIdText" label="学段" >
            </el-table-column>
            <el-table-column prop="textbookVersionIdName" label="版本" >
            </el-table-column>
            <el-table-column prop="notes" label="备注">
            </el-table-column>
            <el-table-column prop="statusText" label="状态">
            </el-table-column>
            <el-table-column prop="id" label="操作" width="120px">
                <template slot-scope="scope" class="aaaa" >
                    <i v-if="!scope.row.showBtn" class="iconfont icon-more" @mouseenter="handleMouseEnter(scope.row)"></i>
                    <ul class="tableOpe operation-table"  v-else @mouseleave="handleMouseLeave(scope.row)">
                        <li class="tableOpe-stop-or-start">
                            <span v-if="scope.row.status == 0" @click="disable(scope.row,1)">停用</span>
                            <span v-else @click="disable(scope.row,0)">启用</span>
                        </li>
                        <li @click="konEditor(scope.row)" class="operation-edu">编辑</li>
                    </ul>
                </template>
            </el-table-column>
            </el-table>


             <!--分页/启用/停用-->
            <el-row class="kno-bnt">
                <el-col :span="5"><div class="grid-content bg-purple cha-title">
                    <stateSwitch @switchL="stateList"></stateSwitch>
                </div></el-col>
                <el-col :span="19"><div class="grid-content bg-purple-light">
                    <kno-paging :pageTotal="pageTotal" @handleSizeChange="SizeChange" @handleCurrentChange="CurrentChange"></kno-paging>
                </div></el-col>
            </el-row>


        <!--点击+新建-->
        <div class="stu-yeaer-modal-add">
            <div class="modalAdd">
                <el-form ref="honForm" :model="honForm" label-width="88px" :rules="formRules">
                      <el-form-item label="学段"  prop="learnsection">
                        <el-select v-model="honForm.learnsection" placeholder="请选择" style="width:100%;" @focus="setSelectMinWidth" @change="getPhaseValue">
                            <el-option :style="{'min-width': selectMinWidth + 2 + 'px'}"  v-for="item in phase" :key="item.code" :label="item.text" :value="item.code"></el-option>
                        </el-select>
                     </el-form-item>



                    <el-form-item label="学科"  prop="subjects">
                        <el-select v-model="honForm.subjects" placeholder="请选择" style="width:100%;" @focus="setSelectMinWidth" @change="getSubjectsValue">
                            <el-option :style="{'min-width': selectMinWidth + 2 + 'px'}" v-for="item in discipline" :key="item.id" :label="item.name" :value="item.id"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="版本"  prop="version">
                        <el-select v-model="honForm.version" placeholder="请选择" style="width: 100%" @focus="setSelectMinWidth">
                            <el-option :style="{'min-width': selectMinWidth + 2 + 'px'}"  v-for="item in versiondata" :key="item.id" :label="item.name" :value="item.id"></el-option>
                        </el-select>
                    </el-form-item>




                     <el-form-item label="年级"  prop="grade"  v-if="isGradeSelect">
                        <el-select v-model="honForm.grade" placeholder="请选择" style="width: 100%" @focus="setSelectMinWidth" @change="getGrade">
                            <el-option :style="{'min-width': selectMinWidth + 2 + 'px'}"  v-for="item in gradeData" :key="item.code" :label="item.text" :value="item.code"></el-option>
                        </el-select>
                    </el-form-item>


                     <el-form-item label="分册"  prop="volume">
                        <el-select v-model="honForm.volume" placeholder="请选择" style="width: 100%" @focus="setSelectMinWidth" @change="getvolumeValue">
                            <el-option :style="{'min-width': selectMinWidth + 2 + 'px'}"  v-for="item in volumedata" :key="item.code" :label="item.text" :value="item.code"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="备注" >
                        <el-input v-model="honForm.note"></el-input>
                    </el-form-item>


                </el-form>
            </div>
        </div>
     </div>
</template>

<script>
import bntList from '../../../components/btn-list'
import stateSwitch from '../../../components/state-switch'
import paging from '../../../components/paging'
export default {

    data(){
        return{
            pageTile:"教材管理",
            //按钮
            model:{
                name:'添加',
                icon:'icon-add'
            },
            modela:{
                name:'导入',
                icon:'icon-input'
            },
            modelc:{
                name:'禁用',
                icon:'icon-stop'
            },


            /*弹框 dialog 开始 */
            honForm:{
                learnsection:"",//学段
                gradeIdName:"",//中文
                subjects:"",//学科
                version:"",//版本
                grade:"",//年级
                volume:"",//分册
                note:"",//备注
            },
            honFormchinesename:{
               learnsectionname:"",//学段中文
               volumename:"",//分测中文
               subjectsname:"",
               gradename:""
            },
            formRules:{
                learnsection:[{required: true, message: '请选择学段', trigger:'change'}],
                subjects:[{required: true, message: '请选择学科', trigger: 'change'}],
                version:[{required: true, message: '请选择版本', trigger: 'change'}],
                grade:[{required: true, message: '请选择年级', trigger: 'change'}],
                volume:[{required: true, message: '请选择分册', trigger: 'change'}],
            },

            isGradeSelect:true,//默认展示年级属性
            phase:[],//学段
            bookversion:[],
            versiondata:[],//版本数据
            gradeData:[],//年级数据
            volumedata:[],//分册


            selectMinWidth:0,//弹框下拉框宽度
            patState:null,//批量启用禁用
            sinState:null,//单个启用禁用
            discipline:[],

            // 顶部查询数据-------
            //查询表单
            formInline:{
                bookname:'',//教材名称
                discipline:'',//学科
                bookversion:"", //教材版本
            },


            // 表格数据
            tableData:[],
            // 停用 分页
            pageSize:10,//显示多少页
            pageCurrent:1,//当前页
            pageTotal:0,//总条数
            status:0,//状态

            value: '',
            multipleSelection:[],

        }
    },

    components:{
        'bnt-list':bntList,
        'stateSwitch':stateSwitch,
        'kno-paging':paging,
    },

    created() {
        this.geTableAllData();  // 表格
        this.dictionary();//新建==》下拉框==》学段
        this.subjectSelect();//
        this.versionApiData();//版本
    },
    methods: {
        //表格中 分页
        geTableAllData(){
            let params={
                "contentName": this.formInline.bookname,
                "pageIndex": this.pageCurrent,
                "pageSize": this.pageSize,
                "subjectId": this.formInline.discipline,
                "textbookVersionId": this.formInline.bookversion,
                status:this.status
            }
            this.$api.tablepaging(params).then(res=>{
                if(res.success==true){

                    this.pageTotal = parseInt(res.totalDatas);
                    let  i = 0;
                    for (i = 0; i < res.data.length; i++) {
                            res.data[i].showBtn = false;
                    }
                    this.tableData=res.data;
                    console.log("res--",res.data)
                }
            })
        },
        goDetails(value){
            //  this.$router.push({path:'/basic/Layout/material-man-details', query: { nameobjdata:value }})
             this.$router.push({path:'/basic/Layout/material-man-details', query: { id:value.id}})

        },

         //字典 学段 年级
        dictionary(){
            let sex = {"type": ['stage','grade','sheetsType']};
            this.$api.dictSelect(sex).then(res => {
                if(res.success == true){
                    let  i = 0;
                    for (i = 0; i < res.data.length; i++) {
                        if(res.data[i].type == 'stage'){
                            this.phase = res.data[i].data;

                        }else if(res.data[i].type == 'grade'){
                             this.gradeData = res.data[i].data;
                        }else if(res.data[i].type == 'sheetsType'){
                             this.volumedata = res.data[i].data;
                        }
                    }
                }
            });
        },


         //学科下拉框数据
        subjectSelect(){
            let params = {};
            this.$api.subjectSelect(params).then(res => {
                if (res.success == true) {
                    this.discipline = res.data;
                }
            })
        },

        //版本下拉框数据
        versionApiData(){
          this.$api.versionSelect().then(res=>{
              if (res.success == true) {
                    this.versiondata = res.data;
                }
          })
        },

        addMaskTableData(){
          let params={
               "gradeId": 0,
                "gradeIdName": "string",
                "id": 0,
                "notes": "string",
                "periodId": 0,
                "periodIdName": "string",
                "sheetsType": 0,
                "sheetsTypeText": "string",
                "subjectId": 0,
                "subjectIdName": "string",
                "textbookVersionId": 0
          }
          this.$api.bookAdd(params).then(res=>{

          })
        },

        // 获取用户选择的学段
        getPhaseValue(value){
           let newGradeData=this.gradeData;
           console.log("学段",value)
            let phaseChname={};
              phaseChname= this.phase.find((item)=>{
                return item.code ==value;
            });
            this.honFormchinesename.learnsectionname=phaseChname.text;
            console.log("学段", this.honFormchinesename.learnsectionname)


           if(value==0){
               this.isGradeSelect=false;
           }else if(value==2||value==3||value==4){
             this.gradeData=this.gradeData.slice(0,3);
             this.isGradeSelect=true;//显示所有的年级
           } else{
             this.isGradeSelect=true;
             this.gradeData=newGradeData;//显示所有的年级
           }
        },




        //  分册
        getvolumeValue(value){
            let obj={};
            obj= this.volumedata.find((item)=>{
                return item.code ==value;
            });
            this.honFormchinesename.volumename=obj.text;
            console.log("分册", this.honFormchinesename.volumename)
        },

        // 学科
        getSubjectsValue(value){
            console.log("学科",value)
            let obj={};
            obj= this.discipline.find((item)=>{
                return item.id ==value;
            });
            this.honFormchinesename.subjectsname=obj.name;
            console.log("学科", this.honFormchinesename.subjectsname)
        },

        // 年级
        getGrade(value){
            let obj = {};
            obj = this.gradeData.find((item)=>{
                return item.code ==value;
            });
            this.honFormchinesename.gradename=obj.text;
            console.log("年级", this.honFormchinesename.gradename)
        },





         // 设置弹窗中下拉框的最小宽度
        setSelectMinWidth(val){
            let target = val.srcElement ? val.srcElement : val.target;
            this.selectMinWidth = target.clientWidth;
        },

        //顶部的添加
         knoAdd(val){
            if(val == 1){
                this.dataAdd();
            }else if(val == 2){
                console.log('导入')
            }else if(val == 4){
                this.patState = {
                    status:1,
                    text:'确定停用所选项'
                };
                this.batState();
            }
        },

        //批量停启用
        batState(){
            let _this = this;
            let userIds = [];
            let i = 0;
            for (i = 0; i < _this.multipleSelection.length; i++) {
                userIds.push(_this.multipleSelection[i].id)
            }
            let params = {
                "status": _this.patState.status,
                "pointIds": userIds
            };
            if(userIds.length == 0){
                _this.$myLayer.errorLayer('至少选择一条数据')
            }else {
                _this.$myLayer.confirmLayer(_this.patState.text,function () {
                    _this.$api.knowledgeState(params).then(res => {
                        if (res.success == true) {
                            _this.$myLayer.successLayer(res.msg)
                            // _this.knowList();
                        } else {
                            _this.$myLayer.errorLayer(res.msg)
                        }
                    })
                })

            }
        },

        //添加
        dataAdd(){
            let _this = this;
            _this.$refs['honForm'].resetFields();
            this.$myLayer.formLayer("新建", $('.stu-yeaer-modal-add'), ['422px'], function () {
                _this.$refs['honForm'].validate((valid) => {
                    if (valid) {
                        // 搜集的参数 params
                        let params = {
                            gradeId:_this.honForm.grade,//年级 code
                            gradeIdText:_this.honFormchinesename.gradename,
                            notes:_this.honForm.note,
                            periodId:_this.honForm.learnsection, //code
                            periodIdText: _this.honFormchinesename.learnsectionname,
                             sheetsType:_this.honForm.volume,
                             sheetsTypeText:_this.honFormchinesename.volumename,

                            subjectId:_this.honForm.subjects,
                            subjectIdName: _this.honFormchinesename.subjectsname,

                            textbookVersionId:_this.honForm.version
                        };
                        console.log("提交后台的参数",params)


                        _this.$api.bookAddData(params).then(res => {
                            if (res.success == true) {
                                _this.geTableAllData();
                                console.log("后端数据",res)

                                _this.$myLayer.successLayer(res.msg)
                            } else {
                                _this.$myLayer.errorLayer(res.msg)
                            }
                        })




                    } else {
                        return false;
                         console.log("不能提交给")
                    }
                });


            })
        },

         //编辑
        konEditor(row){
            let _this = this;
            console.log("123343",row)
            this.$router.push({path:'/basic/Layout/material-man-details', query: { editSwitch: true, editorFlag:false, id:row.id }})
        },

        //查询
        search(val){
            if(val == 1){
                this.geTableAllData();
            }else {
                this.formInline={
                    bookname:'',//教材名称
                    discipline:'',//学科
                    bookversion:"", //教材版本
                }
            }
        },

        handleSelectionChange(val) {
            this.multipleSelection = val;
        },

         /* 鼠标移入移除 */
        /* 鼠标移入移除 */
        handleMouseEnter(row){
            row.showBtn = true;
        },
        handleMouseLeave(row){
            row.showBtn = false
        },


        //状态
        stateList(state){
            this.status = state;
            this.geTableAllData();
        },

        //分页
        SizeChange(val){
            this.pageSize = val;
            this.geTableAllData();
        },
        CurrentChange(val){
            this.pageCurrent = val;
            this.geTableAllData();
        },
        disable(row,val){
                let _this = this;
                if(val == 1){
                    _this.sinState = {
                        status:1,
                        text:'确定停用该类型'
                    };

                }else {
                    _this.sinState = {
                        status:0,
                        text:'确定起用该类型'
                    };
                }
               let params = {
                    id : row.id,
                    status : val
                };

            _this.$myLayer.confirmLayer(_this.sinState.text,function () {
                _this.$api.versionDis(params).then(res => {
                    if (res.success == true) {
                        _this.$myLayer.successLayer(res.msg)
                        _this.geTableAllData();
                    } else {
                        _this.$myLayer.errorLayer(res.msg)
                    }
                })
            })

        }

    },


}
</script>

<style scoped>
    .page-template{
        padding: 18px 24px;
    }
    .kno-title{
        height: 32px;
        border-bottom: 1px solid #e5e7ef;
        margin-bottom: 26px;
    }
    .kno-title .bg-purple-light{
        text-align: right;
    }
    .kno-bnt{
        margin-top: 14px;
    }
    .kno-bnt .bg-purple-light{
        text-align: right;
    }
    .kno-bnt .bg-purple{
        margin-top: 4px;
    }
    .stu-yeaer-modal-add, .tu-yeaer-modal-edit {
        display: none;
    }
    .namefontcolor{
        color:#4A80F6 !important;
    }
    .modalAdd {
        padding: 0px 32px;
        margin: 24px 0px;
    }

    /*操作-显示在同一行 */
    .tableOpe{
        display:flex;
    }
    .tableOpe-stop-or-start{
       margin-right:20px;
       margin-left: 15px;
    }
    .operation-table{
        background: #D0E9FF;
        height: 47px;
        line-height: 47px;
    }


    .operation-table:hover{
        cursor: pointer;
    }
    .operation-edu{
      color: #487ff6;
    }
    tr.el-table__row {
    height: 48px !important;
    }

</style>
<style >
   /* 新建弹出层  一定也要接上这一句  否则 下拉菜单显示不出来 */
   .el-select-dropdown,.el-picker-panel{
        z-index: 999999999!important;
        width: auto;
    }


</style>
